<template>
  <div id="common-box">
      <div class="edit-form">
        <div class="edit-inner">
            <h2>
              <p>修改昵称</p>
              <p class="f14">FIND</p>
            </h2>
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
              <el-form-item label="原昵称" prop="oldNick">
                <el-input v-model="ruleForm.oldNick" disabled="disabled"></el-input>
              </el-form-item>
              <el-form-item label="新昵称" prop="newNick">
                <el-input v-model="ruleForm.newNick"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">确认</el-button>
                <el-button @click="resetForm('ruleForm')">重置</el-button>
              </el-form-item>
            </el-form>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      ruleForm:{
        oldNick:'',
        newNick:'',
        repeatPass:''
      },
      rules:{
        newNick:[
          { required:true,message:'请输入新昵称',trigger:'blur'}
        ]
      }
    }
  },
  methods:{
    submitForm(formVal){
      let that=this;
      this.$refs[formVal].validate((valid)=>{
          if(valid){
            that.$api.editnick({
              nick:that.ruleForm.newNick
            }).then(res=>{
              if(res.err_code === 100){
                that.$message('昵称修改成功');
              }else{
                return that.$message('昵称修改失败')
              }
            })
          }
      })
    },
    resetForm(formName){
      this.$refs[formName].resetFields();
    },
    getUserInfo(){
        this.$api.userinfo().then(res=>{
            if(res.err_code === 100){
                this.ruleForm.oldNick=res.data.user.nickName;
            }
        })
    }
  },
  created(){
    this.getUserInfo();
  }
}
</script>

